import React from 'react';
import PropTypes from 'prop-types';

const propTypes = {
  title: PropTypes.string,
  className: PropTypes.string,
  remove: PropTypes.func,
  color: PropTypes.string,
  children: PropTypes.children
};

const Alert = ({ title, children, color, className, remove }) => {
  return (
    <div
      className={`bg-white border border-${color} text-${color} p-4 pr-12 ${remove &&
        'py-3'} rounded relative my-2 ${className}`}
      role="alert"
    >
      <strong className="font-bold">{title}</strong>
      <span className="block sm:inline">{children}</span>
      {remove && (
        <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
          <svg
            className={`cursor-pointer fill-current h-6 w-6 text-${color}`}
            role="button"
            onClick={remove}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
          >
            <title>Close</title>
            <path d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z" />
          </svg>
        </span>
      )}
    </div>
  );
};

Alert.propTypes = propTypes;
Alert.defaultProps = {
  title: '',
  className: '',
  color: 'secondary',
  remove: null,
  children: []
};

export default Alert;
